$blue: darkslateblue;
$bg-body: #000000;

html,
body {
  height: 100%;
  margin: 0;
  font-family: 'Rubik', sans-serif;
  font-style: italic;
  font-weight: 500;
  overflow: hidden;
}

// A general comment that should be
// stripped out in production
body {
  color: $blue;
  background-color: $bg-body;
  background-image: linear-gradient(to right bottom, #000000, #090606, #110b0c, #161011, #1b1414, #20191a, #251e1f, #2a2325, #302c2e, #373537, #3f3e3f, #474747);
  display: flex;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
}

main {
  display: flex;
  flex-grow: 1;
  align-items: center;
 
  img {
    max-width: none;
  }

}

img {
  max-width: 100%;
  height: auto !important;
}
.xmas-perspective {

  .perspective-on & {
    perspective: 1000px;
    perspective-origin: 50% 50%;
    transform-style: preserve-3d;
    transition: perspective ease 2s;
  }
}

.xmas-card-holder {
  position: relative;
  .perspective-on & {
    transform: rotateX(10deg) rotateY(10deg) scale(0.5);
    transform-style: preserve-3d;
    transition: transform ease 2s;

    .step-01 & {
      transform: rotateX(10deg) rotateY(380deg) scale(0.5);
      transition: transform ease 4s;
      @media only screen and (max-width: 720px) {
        transform: rotateX(10deg) rotateY(380deg) scale(0.4);
      }
      @media only screen and (max-width: 560px) {
        transform: rotateX(10deg) rotateY(380deg) scale(0.2);
      }

      @media (max-width: 1024px) and (orientation: landscape) {
        transform: rotateX(10deg) rotateY(380deg) scale(0.5) translateY(-50%);
      }
    }

    

    .step-03 & {
      transform: rotateX(0) rotateY(360deg) scale(1);
      transition: transform ease 2s;
      @media only screen and (max-width: 1340px) {
        transform: rotateX(0) rotateY(360deg) scale(0.5);
      }
      @media only screen and (max-width: 720px) {
        transform: rotateX(0) rotateY(360deg) scale(0.4);
      }
      @media only screen and (max-width: 560px) {
        transform: rotateX(0) rotateY(360deg) scale(0.25);
      }
      @media only screen and (max-width: 400px) {
        transform: rotateX(0) rotateY(360deg) scale(0.2);
      }

      @media (max-width: 1024px) and (orientation: landscape) {
        
        transform: rotateX(0) rotateY(360deg) scale(0.5) translateY(-50%);

      }
    }
  }
}

$card-width: 1300px;
$card-height: 840px;

.xmas-card {
  background-image: radial-gradient(circle at top left, white 0, #c4c2d6 10%, #8a88af 20%, #505389 50%, #002464 100%);
  width: $card-width;
  height: $card-height;
  display: flex;
  justify-content: center;
  align-items: center;
  backface-visibility: hidden;
  transition: all ease-out 500ms;
  transform: translate3d(0,0,0);
  .is-starting & {
    width: 100vw;
    height: 100vh;
  }
  overflow: hidden;
  position: relative;
  z-index: 1;
  image-rendering: pixelated;

  img {
    image-rendering: pixelated;
  }
  
}
.xmas-card-white {
  width: $card-width;
  height: $card-height;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(to right top, #c4c4c4, #cccccc, #d5d5d5, #dddddd, #e6e6e6, #ebebeb, #efefef, #f4f4f4, #f7f7f7, #f9f9f9, #fcfcfc, #ffffff);
  position: absolute;

  .is-starting & {
    width: 100vw;
    height: 100vh;
  }
  .perspective-on & {
    transform: translateZ(-1px);
    transition: transform linear 2s;
    transform-style: preserve-3d;

  }
}



.xmas-card-back {
  background-color: white;
  width: $card-width;
  height: $card-height;
  position: absolute;

  .is-starting & {
    width: 100vw;
    height: 100vh;
  }
  .perspective-on & {
    transform-origin: 0% 0%;
    transform: rotateX(-10deg);
    transition: transform ease 2s;
    transform-style: preserve-3d;
  }

  .content {
    padding: 100px;
    font-size: 36px;
    font-weight: 400;
    position: relative;
    img {
      position: absolute;
      right:100px;
      width:250px;
    }
  }
}

.xmas-card-front {
  
  .perspective-on & {
    transform-style: preserve-3d;
    transform-origin: 0% 0%;
    transform: rotateX(10deg);
    transition: transform ease 2s;    

    .step-02 & {
      transform: rotateX(170deg);

      @media (max-width: 1024px) and (orientation: landscape) {
        transform: rotateX(170deg);
      }
    }
  }
  .open {
    transform: rotateX(180deg);

  }
}

.scene {
  width: 100%;
  height: 100%;

  @media only screen and (max-width: 1340px) {
    transform: scale(0.75);
